import React from 'react';
import { graphql, Link } from 'gatsby';
import Img from 'gatsby-image';
import { Badge } from 'reactstrap'
import {FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebook, faTwitter, faLinkedin } from '@fortawesome/free-brands-svg-icons'
import { DiscussionEmbed } from 'disqus-react'


import Layout from "../components/layout"
import SEO from '../components/seo'
import { slugify } from '../util/utilityfunctions'


const SinglePost = ({data, pageContext }) => {
    const post = data.markdownRemark.frontmatter
    let featuredImgFluid = post.featuredImage.childImageSharp.fluid
    const baseUrl = 'https://www.kevserozkan.com/'

    const disqusShortname = 'kevserozkan-1'
    const disqusConfig = {
        identifier: data.markdownRemark.id,
        title: post.title,
        url: baseUrl + pageContext.slug
    }

    //author configuration
   
    return (
        <Layout pageTitle={post.title}>
        <div>
        <SEO title={post.title}
        />
                    <Img fluid={featuredImgFluid}/>
                    <span className="date-span">{post.date}</span> {' '}
                     <div className="mt-5 text-config" dangerouslySetInnerHTML={{__html: data.markdownRemark.html}}/>
                     <ul className="post-tags">
                         {post.tags.map(tag => (
                            <li className="badge-display" key={tag} >
                            <Link to={`/tag/${slugify(tag)}`}>
                             <Badge className="badge" pill >{tag}</Badge>
                            </Link>
                            </li>
                    ))}
                </ul>
        </div>
        <h3 className="text-center">Bu Postu Paylan</h3>
        <div className="text-center social-share-links">
            <ul>
                <li>
                    <a href={'https://www.facebook.com/sharer/sharer.php?u=' +
                     baseUrl + pageContext.slug}
                         className="facebook"
                         target="_blank"
                         rel="noopener noreferrer">
                        <FontAwesomeIcon icon={faFacebook} size="2x" />
                    </a>
                </li>
                <li>
                    <a href={'https://www.twitter.com/share?url=' +
                         baseUrl + pageContext.slug + 
                        '&text' + 
                        post.title + 
                        '&via' + 
                        'twitterHandle'
                        }
                         className="twitter"
                         target="_blank"
                         rel="noopener noreferrer">
                        <FontAwesomeIcon icon={faTwitter} size="2x" />
                    </a>
                </li>
                <li>
                    <a href={'https://www.linkedin.com/shareArticle?url=' +
                     baseUrl + pageContext.slug}
                         className="facebook"
                         target="_blank"
                         rel="noopener noreferrer">
                        <FontAwesomeIcon icon={faLinkedin} size="2x" />
                    </a>
                </li>
            </ul>
        </div>
        <DiscussionEmbed shortname={disqusShortname} config={disqusConfig}/>
        </Layout>
        
    )
}


export const postQuery = graphql`
    query BlogPostBySlug($slug: String!) {
        markdownRemark(fields: { slug: { eq: $slug } }) {
        id
        html
        frontmatter {
            title
            date(formatString: "DD MM YYYY, HH:mm", locale: "tr")
            tags
            featuredImage {
                childImageSharp {
                    fluid(maxWidth: 700) {
                        ...GatsbyImageSharpFluid
                    }
                }
            }
        }
    }
}
`

export default SinglePost